import axios from 'axios';
export default {
  props: {
    item: Object
  },

  data() {
    return {
      data: null,
      value: 0
    };
  },

  computed: {
    images() {
      return this.data ? this.data.contents.filter(item => item.class === 'Image') : [];
    }

  },
  methods: {
    fetchData(channelString) {
      this.error = null;
      axios.get(`https://api.are.na/v2/channels/${channelString}`).then(response => {
        if (response) {
          this.data = response.data;
          console.log(this.data);
        } else {
          console.log('error');
        }
      });
    }

  },

  mounted() {
    console.log('MOUNTED');

    if (this.item.class === 'Channel') {
      this.fetchData(this.item.slug);
    }
  }

};