import axios from 'axios';
export default {
  props: {
    channel: Object
  },

  data() {
    return {
      data: null,
      value: 0
    };
  },

  methods: {
    fetchData(channelString) {
      this.error = null;
      axios.get(`https://api.are.na/v2/channels/${channelString}`).then(response => {
        if (response) {
          this.data = response.data;
          console.log(this.data);
        } else {
          console.log('error');
        }
      });
    }

  },

  mounted() {
    this.fetchData(this.channel.slug);
  }

};