import axios from 'axios';
export default {
  data() {
    return {
      root: 'my-online-portfolio-gewbwiq3a8o',
      data: 0,
      value: 2021,
      x: 0
    };
  },

  computed: {
    channels() {
      if (this.data) {
        return this.data.contents.filter(item => item.base_class === 'Channel').sort((a, b) => parseInt(a.title) > parseInt(b.title) ? 1 : -1);
      } else {
        return null;
      }
    },

    range() {
      if (this.channels.length) {
        return [parseInt(this.channels[0].title), parseInt(this.channels[this.channels.length - 1].title), parseInt(this.channels[this.channels.length - 1].title) - parseInt(this.channels[0].title) + 1];
      } else {
        return null;
      }
    }

  },
  methods: {
    fetchData(channelString) {
      this.error = null;
      axios.get(`https://api.are.na/v2/channels/${channelString}`).then(response => {
        if (response) {
          this.data = response.data;
          console.log(this.data);
        } else {
          console.log('error');
        }
      });
    }

  },

  mounted() {
    this.fetchData(this.root);
  }

};